import Geogebra from "react-geogebra";

const ggb = require("../geogebra/fl1.ggb");
function FL1() {
  const xCoord = 0.4;
  const yCoord = -1;
  return (
    <Geogebra
      width="800"
      height="600"
      showMenuBar={false}
      showToolBar={false}
      showAlgebraInput={false}
      filename={ggb}
    />
  );
}

export default FL1;
