import React from "react";
import {
  RevealJS,
  Slide,
  H,
} from "../../../components/revealjs-react/src/index";
import RevealHighlight from "../../../components/revealjs-react/plugins/highlight";
import DifferentialDemo from "../../../components/geogebra-windows/DifferentialDemo";
import { QuestionCircleOutlined } from "@ant-design/icons";
import { Tooltip } from "antd";

function Differential() {
  return (
    <div className="reveal">
      <RevealJS plugins={[RevealHighlight]}>
        <Slide>
          <H>微分的概念</H>
          <p>设函数y = f(x)在x的邻域内有定义,x及x + Δx在此区间内.</p>
          <p>
            如果函数的增量Δy = f(x + Δx) - f(x)可表示为 Δy = AΔx +
            o(Δx)(其中A是不随Δx改变的常量,但A可以随x改变,而o(Δx)是比Δx高阶的无穷小),
            <br />
            那么称函数f(x)在点x是<b>可微的</b>
            ,且AΔx称作函数在点x相应于因变量增量Δy的<b>微分</b>,记作dy,即dy =
            AΔx.
          </p>
          <p>
            函数的微分是函数增量的主要部分,且是Δx的线性函数,故说函数的微分是函数增量的
            <b>线性主部</b>(△x→0).
          </p>
        </Slide>
        <Slide>
          <H>微分的概念</H>
          <p>通常把自变量x的增量 Δx称为自变量的微分,记作dx,即dx = Δx.</p>
          <p>于是函数y = f(x)的微分又可记作dy = f'(x)dx.</p>
          <p>
            函数的微分与自变量的微分之商等于该函数的导数.因此,导数也叫做微商.
          </p>
        </Slide>
        <Slide>
          <H>微分的几何意义</H>
          <p>
            在曲线y=f(x)上取一点M(x0,y0)及其邻近点N(x0+Δx,y0+Δx),过M作曲线y=f(x)的切线.
            <br />
            设切线的倾角为α,切线斜率为tanα=f'(x).
            <br />
            f在点x0处的微分为
            <br />
            dy=f'(x)dx=tanα*Δx=PQ,
            <br />
            Δy=PN,
            <br />
            Δy-dy=PN-PQ=QN.
          </p>
        </Slide>
        <Slide>
          <p>如图所示：</p>
          <Tooltip title={"拖动【滑动条】以观察图像变化"}>
            <QuestionCircleOutlined />
          </Tooltip>
          <DifferentialDemo />
        </Slide>
      </RevealJS>
    </div>
  );
}

export default Differential;
