import React from "react";
import {
  RevealJS,
  Slide,
  H,
} from "../../../components/revealjs-react/src/index";
import RevealHighlight from "../../../components/revealjs-react/plugins/highlight";
import DIDemo from "../../../components/geogebra-windows/DefIntegral";
import { QuestionCircleOutlined } from "@ant-design/icons";
import { Tooltip } from "antd";

function DI() {
  return (
    <div className="reveal">
      <RevealJS plugins={[RevealHighlight]}>
        <Slide>
          <H>定积分</H>
          <p></p>
        </Slide>
        <Slide>
          <H>引入</H>
          <p>设f是闭区间[a,b]上的连续函数,且{"f(x)>=0"}.</p>
          <p>
            由曲线y=f(x),直线x=a,x=b以及x轴所围成的平面图形,称为<b>曲边梯形</b>.
          </p>
          <p>
            对于这个曲边梯形,要测量它的<b>面积</b>,怎么办呢?
          </p>
        </Slide>
        <Slide>
          <p>这里给出一种方法：</p>
          <p>
            1.分割：
            <br />
            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;在竖直方向,把这个<b>曲边梯形</b>
            分割为n份,每一份仍是
            <b>曲边梯形</b>
          </p>
          <p>
            2.代替：
            <br />
            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;以直代曲.用面积相近的小矩形代替这n个
            <b>小曲边梯形</b>.
          </p>
          <p>
            3.求和：
            <br />
            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;将这n个小矩形加起.
          </p>
          <p>
            4.求极限：
            <br />
            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;当每个小矩形的底边趋于零时,所求得的极限就是这个
            <b>曲边梯形</b>的面积.
          </p>
        </Slide>
        <Slide>
          <p>如图所示：欲求灰色部分面积.</p>
          <Tooltip title={"尝试点击【继续】以观察图像变化"}>
            <QuestionCircleOutlined />
          </Tooltip>
          <DIDemo />
        </Slide>
      </RevealJS>
    </div>
  );
}

export default DI;
