import React from "react";
import {
  RevealJS,
  Slide,
  H,
} from "../../../components/revealjs-react/src/index";
import RevealHighlight from "../../../components/revealjs-react/plugins/highlight";
import FL1 from "../../../components/geogebra-windows/FL1";
import { QuestionCircleOutlined } from "@ant-design/icons";
import { Tooltip } from "antd";

const text = "prompt text";
function FLConcept() {
  return (
    <div className="reveal">
      <RevealJS plugins={[RevealHighlight]}>
        <Slide>
          <H size="1">函数的极限</H>
          <p>
            <b>定义：</b>如果对于任意给定的正数e(不论它多么小),总存在正数d
            ,使得当|x-x0|{"<"}d时,恒有不等式|f(x)-A|{"<"}e成立,
            <br />
            那么就称常数A是函数f(x)的极限,或者称函数f(x)收敛于A,记为{"f(x)->A"}
            <br />
            反之，如果函数没有极限,就说函数是发散的.
          </p>
        </Slide>

        <Slide>
          {/* <div className="section"> */}
          <p>
            定义中“得当|x-x0|{"<"}d时,恒有不等式|f(x)-A|{"<"}
            e成立”这句话是指：
            <br />
            落在区间(x0-d,x0+d)中的所有x,都满足不等式|f(x)-A|
            {"<"}e.
            <br />
            即区间(x0-d,x0+d)在f的作用下,统统落在区间(A-e,A+e)中.
            <br />
            如图所示：无论e如何改变,总有无限项落在区间(A-e,A+e)中.
            <br />
          </p>
          <Tooltip title={"拖动【滑动条】以观察图像变化"}>
            <QuestionCircleOutlined />
          </Tooltip>
          <FL1 />
          {/* </div> */}
        </Slide>
      </RevealJS>
    </div>
  );
}

export default FLConcept;
