import React from "react";
import {
  RevealJS,
  Slide,
  H,
} from "../../../../components/revealjs-react/src/index";
import RevealHighlight from "../../../../components/revealjs-react/plugins/highlight";
import LagrangeDemo from "../../../../components/geogebra-windows/LagrangeDemo";
import { QuestionCircleOutlined } from "@ant-design/icons";
import { Tooltip } from "antd";

function Lagrange() {
  return (
    <div className="reveal">
      <RevealJS plugins={[RevealHighlight]}>
        <Slide>
          <H size="1">拉格朗日中值定理</H>
          <p>
            如果y=f(x)满足：
            <br />
            (1)在区间[a,b]上连续
            <br />
            (2)在区间(a,b)内可导
          </p>
          <p>则至少存在一点ξ∈(a,b),使得f'(ξ)=(f(b)-f(a))/(b-a)</p>
        </Slide>
        {/* <Slide>
          <p></p>
        </Slide> */}
        <Slide>
          <p>
            拉格朗日中值定理的几何意义：
            <br />
            如果连续曲线y=f(x)的弧AB上除端点外处处具有不垂直于X轴的切线,那么这弧上至少有一点C,使曲线在C点处的切线平行于弦AB.
          </p>
          <Tooltip title={"尝试点击【开始】以观察图像变化"}>
            <QuestionCircleOutlined />
          </Tooltip>
          <LagrangeDemo />
        </Slide>
      </RevealJS>
    </div>
  );
}
export default Lagrange;
