import { Layout, Breadcrumb } from "antd";
import SLConcept from "../views/subviews/sequence-limit/SLConcept";
import FLConcept from "../views/subviews/function-limit/FLConcept";
import Differential from "../views/subviews/differential/Differential";
import Lagrange from "../views/subviews/mean-value-theorem/lagrange/Lagrange";
import DI from "../views/subviews/definite-integral/DI";
import Concept from "../views/subviews/concept/Concept";
import History from "../views/subviews/history/History";

const { Content } = Layout;

function Option1(props) {
  console.log("Option1 : ", props);
  if (props.menuKey === "1") {
    return (
      <Layout style={{ padding: "0 24px 24px" }}>
        <SLConcept />
      </Layout>
    );
  } else if (props.menuKey === "2") {
    return (
      <Layout style={{ padding: "0 24px 24px" }}>
        <FLConcept />
      </Layout>
    );
  } else if (props.menuKey === "16") {
    return (
      <Layout style={{ padding: "0 24px 24px" }}>
        <Differential />
      </Layout>
    );
  } else if (props.menuKey === "17") {
    return (
      <Layout style={{ padding: "0 24px 24px" }}>
        <Lagrange />
      </Layout>
    );
  } else if (props.menuKey === "22") {
    return (
      <Layout style={{ padding: "0 24px 24px" }}>
        <DI />
      </Layout>
    );
  } else if (props.menuKey === "-1") {
    return (
      <Layout style={{ padding: "0 24px 24px" }}>
        <Concept />
      </Layout>
    );
  } else if (props.menuKey === "-2") {
    return (
      <Layout style={{ padding: "0 24px 24px" }}>
        <History />
      </Layout>
    );
  } else {
    return (
      <Layout style={{ padding: "0 24px 24px" }}>
        <Breadcrumb style={{ margin: "16px 0" }}>
          <Breadcrumb.Item>Home</Breadcrumb.Item>
          <Breadcrumb.Item>List</Breadcrumb.Item>
          <Breadcrumb.Item>App</Breadcrumb.Item>
        </Breadcrumb>
        <Content
          className="site-layout-background"
          style={{
            padding: 24,
            margin: 0,
            minHeight: 280,
          }}
        >
          {props.menuKey}
        </Content>
      </Layout>
    );
  }
}

export default Option1;
