import React from "react";
import {
  RevealJS,
  Slide,
  H,
} from "../../../components/revealjs-react/src/index";
import RevealHighlight from "../../../components/revealjs-react/plugins/highlight";
import SL2 from "../../../components/geogebra-windows/SL2";
import "./SLConcept.css";
import { QuestionCircleOutlined } from "@ant-design/icons";
import { Tooltip } from "antd";

function SLConcept() {
  return (
    <div className="reveal">
      <RevealJS plugins={[RevealHighlight]}>
        {/* <Slide>
          <div class="content">
            <H>目录</H>
            <p>1.数列的概念</p>
            <p>2.数列的极限</p>
          </div>
        </Slide> */}

        {/* <Slide>
          <Slide>
            <H>I.数列的概念</H>
            <p>
              定义:按自然数1,2,3,...编号依次排列的一列数
              <br />
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;x1,x2,x3,...,xn,...
              <br />
              称为<b>无穷</b>数列,简称数列,记作.
              <br />
              数列的每个数称为数列的项, 第n项xn称为通项(一般项).
            </p>
          </Slide>
          <Slide>
            <SL2 />
          </Slide>
        </Slide> */}

        <Slide>
          <H size="1">数列的极限</H>
          <p>
            <b>定义：</b>如果对于任意给定的正数e(不论它多么小),总存在正数N
            ,使得当n{"<"}N时,恒有不等式|xn-A|{"<"}e成立,
            <br />
            那么就称常数A是数列{"{an}"}的极限,或者称数列{"{an}"}
            收敛于A,记为{"an->A"}
            <br />
            反之，如果数列没有极限,就说数列是发散的.
          </p>
        </Slide>

        <Slide>
          {/* <div class="section"> */}
          <p>
            定义中“使得当n{"<"}N时,恒有不等式|xn-A|{"<"}
            e成立”这句话是指：
            <br />
            凡是下标大于N的所有xn,都满足不等式|xn-A|{"<"}
            e.
            <br />
            即这些xn统统落在区间(A-e,A+e)中.
            <br />
            如图所示：无论e如何改变,总有无限项落在区间(A-e,A+e)中.
            <br />
          </p>
          <Tooltip title={"尝试拖动【滑动条】以观察图像变化"}>
            <QuestionCircleOutlined />
          </Tooltip>
          <SL2 />
          {/* </div> */}
        </Slide>

        <Slide>
          <div>
            <p>数列极限具有以下性质：</p>
            <p>
              性质1&nbsp;如果数列有极限，则极限是唯一的. <br />
              性质2&nbsp;收敛的数列必定有界. <br />
              &nbsp;(注意：有界性是数列收敛的必要条件) <br />
              &nbsp;推论：无界数列必定发散. <br />
              性质3&nbsp;收敛数列的保号性. <br />
              性质4&nbsp;收敛数列的任一子数列收敛于同一极限
            </p>
          </div>
        </Slide>
      </RevealJS>
    </div>
  );
}

export default SLConcept;
