import React from "react";
import {
  RevealJS,
  Slide,
  H,
} from "../../../components/revealjs-react/src/index";
import RevealHighlight from "../../../components/revealjs-react/plugins/highlight";

function History() {
  return (
    <div className="reveal">
      <RevealJS plugins={[RevealHighlight]}>
        <Slide>
          <H>微积分发展史</H>
          <p>————从积分，到微分，再到极限</p>
        </Slide>
        <Slide>
          <H>引入</H>
          <p>
            我们课本里面学习微积分的过程，都是从极限开始，从导数过度到微分，再到积分。
            <br />
            而事实恰恰相反，微积分的发展经历了从积分，到微分，再到极限的过程。
          </p>
        </Slide>
        <Slide>
          <H>萌芽</H>
          <p>
            战国时期，<b>庄子</b>提出“一尺之棰，日取其半，万世不竭”。
          </p>
          <p>
            魏晋时期，<b>刘徽</b>
            发明了“割圆术”，他通过对直线进行无穷小分割，然后求其极限状态的和，来解决圆面积的问题。
          </p>
          <p>
            南北朝时期，<b>祖暅</b>
            沿用了刘徽的思想，利用刘徽“牟合方盖”的理论去进行体积计算，得出“幂势相同，则积不容异”的结论（即“祖暅原理”）。
          </p>
        </Slide>
        <Slide>
          <H>萌芽</H>
          <p>
            <b>安蒂丰</b>
            发现了“穷竭法”，即“对于任意两个不等量，若从较大量中减去大于其半的量，再从所余量中减去大于其半的量，重复这一过程，则所余之量必小于原来较小的量”。
          </p>
          <p>
            <b>欧多克斯</b>发展了“穷竭法”，被认为是微积分的第一步。
          </p>
          <p>
            <b>阿基米德</b>
            阐明了“平衡法”，它的基本思想是“将需要求的未知量（面积、体积等）分成许多微小单元（如微小线段、薄片等），再用另一组微小单元（总和比较容易计算）来比较，但需要建立一个杠杆，使前后两组获得平衡”。
          </p>
        </Slide>
        <Slide>
          <H>酝酿</H>
          <p>近代微积分的酝酿主要发生在17世纪前半叶。</p>
        </Slide>
        <Slide>
          <H>酝酿</H>
          <p>积分方面：</p>
          <p>
            <b>开普勒</b>
            主要研究了各种旋转体的性质，他把无穷小的弧看成直线，把无限窄的面看成直线，把无限薄的体看作面，这蕴含着朴素的积分思想。
          </p>
          <p>
            <b>卡瓦列利</b>
            建立了“不可分量原理”，开始系统地运用无穷小方法计算面积和体积，提出了“卡瓦列利原理”，并利用它巧妙地求出了若干曲边图形的面积，以及证明了旋转体的表面积和体积公式。
          </p>
          <p>
            <b>托里拆利</b>
            进一步发展了不可分量原理，使它走向牛顿和莱布尼茨创立的微积分学。
          </p>
          <p>
            <b>沃利斯</b>
            是在牛顿和莱布尼茨之前用“代数”的方法发展微积分贡献最突出的数学家。他提出算术不可分法并使用它获得了许多重要结果，对微积分的创立具有重要意义。
          </p>
        </Slide>
        <Slide>
          <H>酝酿</H>
          <p>微分方面：</p>
          <p>
            <b>费马</b>
            是较早研究曲线的切线问题的数学家，他具体给出了求切线的方法。尽管这种方法缺乏严密性，但它具有微分学的现代标准形式。
          </p>
          <p>
            <b>笛卡尔</b>
            创立了解析几何，改变了自古希腊以来代数和几何分离的趋向，改变了以往单纯采用几何方法研究积分的状况，还提出了“圆法”，而这种代数方法是后来求切线方法的雏形。
          </p>
          <p>
            <b>巴罗</b>在《几何学讲义》中提出了用几何方法求切线—微分三角。
          </p>
        </Slide>
        <Slide>
          <H>创立</H>
          <p>
            17世纪下半叶，<b>牛顿</b>和<b>莱布尼茨</b>
            在前人基础上各自创立了微积分。
          </p>
          <p>
            <b>牛顿</b>
            得到人类有史以来最有力的数学工具——微分方法及其思想，也称为“流数法”。之后他又创造了反流数法，牛顿是历史上第一次以明确形式给出了微积分的一个重大理论——微积分基本定理。
          </p>
          <p>
            <b>莱布尼茨</b>
            从发现和揭示微分原理入手发展他的学说，第一次表达了微分和积分的关系。
          </p>
        </Slide>
        <Slide>
          <H>创立</H>
          <p>
            微分和积分的统一，是微积分创立的一个重要标志，微积分基本定理的确定，使微积分的理论更加壮阔。
          </p>
        </Slide>
        <Slide>
          <H>完善</H>
          <p>
            尽管<b>牛顿</b>和<b>莱布尼茨</b>
            创立了微积分，但是微积分的基础是不稳固的。
          </p>
        </Slide>
        <Slide>
          <H>完善</H>
          <p>
            19世纪以前，在微积分的发展过程中，其数学分析的严密性问题一直没有得到解决。
          </p>
          <p>
            18世纪中，包括牛顿和莱布尼兹在内的许多大数学家都觉察到这一问题并对这个问题作了努力，但都没有成功地解决这个问题。
          </p>
          <p>
            整个18世纪，微积分的基础是混乱而不清楚的，许多数学家甚至因此而怀疑微积分的全部工作。
          </p>
        </Slide>
        <Slide>
          <H>完善</H>
          <p>
            从19世纪开始，<b>柯西</b>、<b>魏尔斯特拉斯</b>
            等人进行了微积分理论的严格化工作，致力于完善微积分柯西对微积分的基本概念，如函数、极限、导数、微分等都给出了明确的定义，使得极限代替了无穷小，建立极限理论，这样就使微积分建立在极限基础之上了。
          </p>
          <p>
            <b>戴德金</b>、<b>康托尔</b>等人创立了实数理论；<b>康托尔</b>
            又创立了集合论。
          </p>
        </Slide>
        <Slide>
          <H>总结</H>
          <p>
            这样，微积分就建立在<b>极限理论</b>、<b>实数理论</b>和<b>集合论</b>
            的稳固基础之上了，至此微积分学基本臻于完善。
          </p>
        </Slide>
        <Slide>
          <H>总结</H>
          <p>
            这就是今天我们看到的<b>微积分</b>。
          </p>
        </Slide>
      </RevealJS>
    </div>
  );
}

export default History;
