import React, { useState } from "react";
import { Layout, Menu } from "antd";
import {
  UserOutlined,
  LaptopOutlined,
  NotificationOutlined,
} from "@ant-design/icons";

import Option1 from "../components/Options";
import MenuContext from "antd/lib/menu/MenuContext";
import MenuDivider from "antd/lib/menu/MenuDivider";
import MenuItem from "antd/lib/menu/MenuItem";

const { SubMenu } = Menu;
const { Header, Sider } = Layout;

function Main() {
  const [leftMenu, setLeftMenu] = useState("1");
  const [topMenu, setTopMenu] = useState("1");

  const handleTopMenuClick = (value) => {
    console.log("handleTopMenuClick : ", value);
    setTopMenu(value.key);
  };
  function handleLeftMenuClick(value) {
    console.log("handleLeftMenuClick : ", value);
    setLeftMenu(value.key);
  }

  return (
    <Layout>
      <Header className="header">
        <div className="logo" />
        <Menu
          theme="dark"
          mode="horizontal"
          defaultSelectedKeys={["1"]}
          onClick={handleTopMenuClick}
        >
          <Menu.Item key="1">发展史</Menu.Item>
          <Menu.Item key="2">一元微积分</Menu.Item>
          {/* <Menu.Item key="3">nav 3</Menu.Item> */}
        </Menu>
      </Header>

      <Layout>
        <Sider width={200} className="site-layout-background">
          <Menu
            mode="inline"
            defaultSelectedKeys={["1"]}
            defaultOpenKeys={["sub1"]}
            style={{ height: "100%", borderRight: 0 }}
            onClick={handleLeftMenuClick}
          >
            <SubMenu key="sub1" icon={<UserOutlined />} title="极限">
              <Menu.Item key="1">数列极限</Menu.Item>
              <Menu.Item key="2">函数极限</Menu.Item>
              {/* <Menu.Item key="3">存在条件</Menu.Item> */}
              {/* <Menu.Item key="4">无穷大量与无穷小量</Menu.Item> */}
              {/* <Menu.Item key="5">函数的连续性</Menu.Item> */}
            </SubMenu>

            <SubMenu
              key="sub4"
              icon={<NotificationOutlined />}
              title="一元函数微分学"
            >
              {/* <Menu.Item key="12">导数的概念</Menu.Item>
              <Menu.Item key="13">求导法则</Menu.Item>
              <Menu.Item key="15">高阶导数</Menu.Item> */}
              <Menu.Item key="16">微分的概念</Menu.Item>
              <Menu.Item key="17">拉格朗日定理</Menu.Item>
              {/* <Menu.Item key="18">柯西中值定理</Menu.Item>
              <Menu.Item key="19">洛必达法则</Menu.Item> */}
            </SubMenu>

            <SubMenu
              key="sub6"
              icon={<NotificationOutlined />}
              title="一元函数积分学"
            >
              {/* <Menu.Item key="21">不定积分</Menu.Item> */}
              <Menu.Item key="22">定积分</Menu.Item>
            </SubMenu>

            <SubMenu
              key="sub0"
              icon={<NotificationOutlined />}
              title="微积分简介"
            >
              {/* <Menu.Item key="21">不定积分</Menu.Item> */}
              {/* <Menu.Item key="-1">微积分的概念</Menu.Item> */}
              <Menu.Item key="-2">微积分的简史</Menu.Item>
              {/* <Menu.Item key="-3">微积分的应用</Menu.Item> */}
            </SubMenu>
          </Menu>
        </Sider>

        <Option1 menuKey={leftMenu} />
      </Layout>

      {/* <Layout>
        <Sider>
          <Menu
            mode="inline"
            defaultSelectedKeys={["1"]}
            defaultOpenKeys={["sub1"]}
            style={{ height: "100%", borderRight: 0 }}
            onClick={handleLeftMenuClick}
          >
            <SubMenu icon={<UserOutlined />} title="开始">
              <MenuItem>eee</MenuItem>
            </SubMenu>
          </Menu>
        </Sider>
      </Layout> */}
    </Layout>
  );
}

export default Main;
